<template>
  <div class="signin">
      <input type="text" v-model="email" placeholder="    Email">
            <input type="password" v-model="password" placeholder="    Password">
            <button @click="sendfuc">Sign in</button>
  </div>
</template>

<script>
// @ is an alias to /src
export default {
	name: "signin",
	data() {
		return {
			email: "",
			password: "",
		};
	},
	methods: {
		sendfuc() {
			if (this.email == "" || this.password == "") {
				alert("请按要求填写信息");
				return false;
			}
			//console.log("email:" + this.email + "\npassword:" + this.password);
			this.axios
				.post("/api/test", this.qs.stringify({ email: this.email, password: this.password }), {
					//   headers: {
					//     'Content-Type': 'application/x-www-form-urlencoded'
					//   }
				})
				.then(
					function(res) {
						//返回数据
						console.log(res.data);
					}.bind(this)
				)
				.catch(
					//错误捕获
					function(err) {
						if (err.response) {
							console.log(err.response);
						}
					}.bind(this)
				);
		},
	},
};
</script>
