<template>
  <div class="signup">
    <input type="text" name="Email" v-model="email" placeholder="    Email">
    <input type="password" name="Password" v-model="password" placeholder="    Password">
    <input type="password" name="Repeat" v-model="passwordrepeat " placeholder="    Repeat Password">
    <button @click="checkrepeat">Sign up</button>
  </div>
</template>
<script>
export default {
	data() {
		return {
			email: "",
			password: "",
			passwordrepeat: "",
		};
	},
	methods: {
		checkrepeat() {
			if (
				this.email == "" ||
				this.password == "" ||
				this.passwordrepeat == "" ||
				this.email != this.passwordrepeat
			) {
				alert("请按要求填写信息");
				return false;
			}
			// console.log(
			// 	"email:" + this.email + "\npassword:" + this.password + "\nrepeatpassword:" + this.passwordrepeat
			// );
			this.axios
				.post("/api/test", this.qs.stringify({ email: this.email, password: this.password }), {
					//   headers: {
					//     'Content-Type': 'application/x-www-form-urlencoded'
					//   }
				})
				.then(
					function(res) {
						//返回数据
						console.log(res.data);
					}.bind(this)
				)
				.catch(
					//错误捕获
					function(err) {
						if (err.response) {
							console.log(err.response);
						}
					}.bind(this)
				);
		},
	},
};
</script>
